
.topbar{
    width: 100%;
    height: 10vh;
    background-color: white;
    position: fixed;
    top: 0;
    z-index: 3;
    transition: all 1s ease;
    overflow: hidden;
}

.wrapper{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 30px;
}

.left{
    display: flex;
    align-items: center;
}

.left a{
    text-decoration: none;
    color: black;
}

.left .logo{
    font-size: 35px;
    font-weight: 800;
    text-decoration: none;
    margin-right: 30px;
}

.item-container{
    display: flex;
    align-items: center;
}



.item-container .icon{
    font-size: 18px;
    margin-right: 5px;
}

.item-container span{
    font-size: 18px;
    font-weight: 600;
}

.item-container span:hover{
    color: #7027A0;
}

.right .hamburger{
    width: 32px;
    height: 25px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    cursor: pointer;
}

.hamburger span{
    width: 100%;
    height: 3px;
    background-color: #7027A0;
    transform-origin: left;
    transition: all 1s ease;
}

.active .left a{
    color: white;
}

.active{
    background-color: #7027A0;
    color: white;
}

.active .hamburger span:first-child{
    background-color: white;
    transform: rotate(45deg);

}

.active .hamburger span:last-child{
    background-color: white;
    transform: rotate(-45deg)
}

:active .hamburger span:nth-child(2){
    opacity:0;
}


@media (max-width: 767px) {

    .item-container{
        display: none;
    }
    .left .logo{
        font-size: 25px;
        font-weight: 800;
    }
}
